import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        /**
         * 是否需要强制登录
         */
        forcedLogin: true,
        hasLogin: false,
        userName: "",
		userInfo: null,
    },
    mutations: {
        login(state, user) {
            state.userName = user.username || '新用户';
            state.hasLogin = true;
			state.userInfo = user;
        },
        logout(state) {
            state.userName = "";
            state.hasLogin = false;
        },
		checkLogin(){
			let user = service.getUsers()
			console.log('' + JSON.stringify(user));
			if (user.userid != null) {
				state.userName = user.username || '新用户';
				state.hasLogin = true;
				state.userInfo = user;
			}
		},
    }
})

export default store
